'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import {
    LayoutDashboard,
    Users,
    CreditCard,
    Settings,
    LogOut,
    BarChart3
} from 'lucide-react';

const navItems = [
    { href: '/dashboard', icon: LayoutDashboard, label: 'Dashboard' },
    { href: '/providers', icon: Users, label: 'Providers' },
    { href: '/billing', icon: CreditCard, label: 'Billing' },
];

export function Sidebar() {
    const pathname = usePathname();

    return (
        <div className="hidden lg:flex lg:w-72 lg:flex-col lg:fixed lg:inset-y-0 z-50">
            <div className="flex flex-col flex-grow bg-white border-r border-gray-200 pt-8 pb-4 overflow-y-auto shadow-ultra">
                <div className="flex items-center px-6 mb-10">
                    <BarChart3 className="h-8 w-8 text-primary text-blue-700" />
                    <span className="ml-3 text-2xl font-bold text-gradient text-blue-600">USSD Portal</span>
                </div>

                <nav className="flex-1 px-4 space-y-2">
                    {navItems.map((item) => {
                        const Icon = item.icon;
                        const isActive = pathname === item.href;

                        return (
                            <Link
                                key={item.href}
                                href={item.href}
                                className={`
    flex items-center px-4 py-3 text-sm font-medium rounded-xl
    transition-all duration-200 ease-in-out group
    ${isActive
                                        ? 'bg-blue-700 text-white shadow-lg shadow-blue-700/25'
                                        : 'text-gray-600 hover:bg-blue-700 hover:text-white'
                                    }
  `}
                            >
                                <Icon className={`mr-3 h-5 w-5 ${isActive ? 'text-white' : 'text-gray-400 group-hover:text-white'}`} />
                                {item.label}
                                {isActive && <div className="ml-auto w-2 h-2 bg-white rounded-full"></div>}
                            </Link>

                        );
                    })}
                </nav>

                <div className="px-4 mt-8 space-y-2">
                    <button className="flex items-center w-full px-4 py-3 text-sm font-medium text-gray-600 rounded-xl hover:bg-gray-100 hover:text-gray-900 transition-all">
                        <Settings className="mr-3 h-5 w-5 text-gray-400" />
                        Settings
                    </button>
                    <button className="flex items-center w-full px-4 py-3 text-sm font-medium text-red-600 rounded-xl hover:bg-red-50 transition-all">
                        <LogOut className="mr-3 h-5 w-5" />
                        Logout
                    </button>
                </div>
            </div>
        </div>
    );
}