'use client';

import { Bell, Search, User } from 'lucide-react';
import { useState } from 'react';

export function Header() {
    const [search, setSearch] = useState('');

    return (
        <header className="sticky top-0 z-40 flex h-16 items-center gap-4 border-b bg-white/95 backdrop-blur supports-[backdrop-filter]:bg-white/60 px-6 lg:pl-80">
            <div className="flex flex-1 items-center gap-4">
                <div className="relative flex-1 max-w-md">
                    <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
                    <input
                        type="search"
                        placeholder="Search clients, invoices, providers..."
                        className="w-full pl-10 pr-4 py-2 bg-gray-50 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-primary/20 focus:border-primary transition-all"
                        value={search}
                        onChange={(e) => setSearch(e.target.value)}
                    />
                </div>
            </div>

            <div className="flex items-center gap-3">
                <button className="relative p-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-xl transition-all">
                    <Bell className="h-5 w-5" />
                    <span className="absolute top-1 right-1 h-2 w-2 bg-red-500 rounded-full"></span>
                </button>

                <div className="h-8 w-px bg-gray-200"></div>

                <button className="flex items-center gap-3 p-2 hover:bg-gray-100 rounded-xl transition-all">
                    <div className="h-8 w-8 bg-gradient-to-br from-primary to-purple-600 rounded-lg flex items-center justify-center">
                        <User className="h-5 w-5 text-white" />
                    </div>
                    <div className="hidden md:block text-left">
                        <p className="text-sm font-medium">Admin User</p>
                        <p className="text-xs text-gray-500">Administrator</p>
                    </div>
                </button>
            </div>
        </header>
    );
}